body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.jumbotron {
    width: 60%;
    margin: 2.5% auto;
}

.bold-text {
    font-weight: bold;
}
.jumbotron {
    width: 60%;
    margin: 2.5% auto;
}
.jumbotron, .class-listing-display {
    width: 60%;
    margin: 2.5% auto;
}

.class-filter {
    width: 60%;
    margin: 0 auto;
}

.italics-text {
    font-style: italic;
}

.instructor-img {
    width: 110px;
    height: 110px;
    float: right;
}

.card {
    margin-top: 3%;
}

.pagination {
    margin: 20px auto;
}

.pagination li{
    padding: 20px;
}

.pagination a {
    color: #17a2b8;
    font-size: 20px;
}

.active{
    background-color: #17a2b8;
}

.active a {
    color: white;
}
#create-form {
    width: 60%;
    margin: auto;
}

form {
	width: 80%;
	margin: auto;
}

input[type=text], input[type=number] {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

input[type=submit], input[type=button] {
  width: 40%;
  background-color: #4CAF50;
  color: white;
  padding: 14px 20px;
  margin: 5%;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
#myaccount-page{
  width: 60%;
  margin: auto;
}

form {
	width: 80%;
	margin: auto;
}

input[type=text], input[type=number] {
  width: 500px;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

input[type=submit], input[type=button] {
  width: 200px;
  background-color: #4CAF50;
  color: white;
  padding: 14px 20px;
  margin: 5%;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.nav-link {
    cursor: pointer;
    font-weight: bold;
}

.navbar-brand {
	font-weight: bold;
}

.navbar-nav .btn {
	margin-left: 10px;
}

.bg-primary {
	background-color: #17a2b8 !important;
}
